@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap');

:root{
--sidebar-flex:1.5;
--condiv-flex: 6;
--fontsize-p: 1.2rem;
--fontsize-nav: 1.6rem;
--typing-font: 3rem;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway';
}

.App {
  display: flex;
  width: 100vw;
  min-height: 100vh;
}

/* .condiv class is for the right side content container */

.condiv {
  padding: 100px;
  background-color: #f4f5f9;
  flex:var(--condiv-flex);
}

/* Home CSS */
.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.profilepic {
  border-radius: 50%;
  width: 200px;
  margin: 30px;
}

.typingeffect { 
  font-size: var(--typing-font);
  margin-bottom: 30px;

}

/* NavBar style */
nav {
  padding: 60px 30px 0px 30px;
  flex:var(--sidebar-flex);
  background: rgb(238,103, 143);
  background: linear-gradient(180deg, rgba(238, 103, 143, 1) 50%, rgba (132, 124, 252, 1) 80%);
}

/* nav ul style */
nav ul {
  font-size: var(--fontsize-nav);
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}

nav ul li{
  margin: 40px;
}

a {
  text-decoration: none;
  color: white;
}

/* whichever NavItem is clicked it will be styled bold */
.active{
  font-weight: bolder;
}

/* style for social.js */

.social {
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;
}

.social, i {
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}

.social i:hover
{
  color: #ffffff;
}

/* About component CSS */

.subtopic {
  margin: 10px 10px 30px 10px !important;
}

p {
  font-size: var(--fontsize-p);
}

h1, h2, h3, h4, p {
  margin: 10px;
}

/* WideCard component CSS */

.widecard {
  border: 0px solid #9a9b9c;
  display: flex;
  margin: 30px 0px 30px 0px;
}

.widecard:hover {
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23)
}

.contactLink {
  color: black;
  
}

.contactLink:hover {
  color: blue;
}

h2 {
  color: black;
}
h2:hover {
  color: blue;
}

.links {
  color: black;
}

.links:hover {
  color: blue;
}




@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
  }
  .App
  {
    flex-direction: column;
  }
  .sidebar
  {
    padding: 30px;
    background: rgb(238,103,143);
    background: linear-gradient(90deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
  }
  .condiv
  {
    padding: 50px ;
  }
  nav ul
  {
    display: flex;
  }
  nav ul li
  {
    margin: 10px;
  }
  .social
  {
    left:0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    bottom: 30px;
  }
  .social i
  {
    padding-right: 0px;
  }
}